import React, { Component } from 'react';
import axios from 'axios';
import { format } from 'timeago.js';
import {Link} from 'react-router-dom';

class NotesList extends Component {
    
    state = {
        notes: []
    }

    async getNotes(){
        const res = await axios.get('http://localhost:4000/api/notes')
        this.setState({notes: res.data});
    }

    async componentDidMount(){
        
        this.getNotes()
    }

    deleteNote = async (id) => {
        await axios.delete('http://localhost:4000/api/notes/'+id);
        this.getNotes();
    }
    
    render() {
        return (
            <div className="row">
                {
                    this.state.notes.map(note => (
                        <div className="col-md-4 p-2" key={note.id}>
                            <div className="card">
                                
                                <div className="card-body">
                                <h3>{note.title}</h3>
                                    <p>{note.content}</p>
                                    <p>{note.author}</p>
                                    <p>{format(note.date)}</p>
                                    <Link className="btn btn-info" to={"/edit/"+note.id}>Edit</Link>&nbsp;&nbsp;
                                    <button className="btn btn-danger" onClick={() => this.deleteNote(note.id)}>Delete</button>
                                </div>
                                <div className="card-footer bg bg-warning p-1"></div>
                            </div>
                        </div>
                    ))
                }
            </div>
        );
    }
}

export default NotesList;